.reveal-modal {
  border-radius: 3px;
  display: none;
  position: absolute;
  top: 0;
  //visibility: hidden;
  width: 100%;
  z-index: 1005;
  left: 0;
  background-color: #FFFFFF;
  padding: 1.875rem;
  border: solid 1px #666666;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.4);
  @media only screen and (min-width: 40.0625em) {
    left: 0;
    margin: 0 auto;
    max-width: 62.5rem;
    right: 0;
    width: 80%;
  }
}

.reveal-modal .info {
  padding: 2rem 1rem 0;
}
.reveal-modal .info .attribution {
  color: rgb(73, 85, 96);
  font-family: "Ubuntu";
  line-height: 75px;
}
.reveal-modal .info .attribution b {
  font-weight: 700;
}
.reveal-modal .info .avatar {
  border-radius: 50%;
  float: left;
  height: 75px;
  margin-right: 1rem;
  width: 75px;
}

.reveal-modal-bg {
  background: #000000;
  background: rgba(0, 0, 0, 0.45);
  bottom: 0;
  display: none;
  left: 0;
  position: fixed;
  right: 0;
  top: 0;
  z-index: 1004;
  left: 0;
}

.reveal-modal .close-reveal-modal {
  color: #AAAAAA;
  cursor: pointer;
  font-size: 2.5rem;
  font-weight: bold;
  line-height: 1;
  position: absolute;
  top: 0.625rem;
  right: 1.375rem;
}